import React from 'react';
// Make sure schema.json is in the same 'app' folder!
import data from './schema.json';

export default function PortfolioPage() {
  // Defensive check: if data isn't loading correctly
  if (!data || !data.sections) {
    return <div>Loading your blueprint...</div>;
  }

  return (
    <div style={{ 
      backgroundColor: data.theme.darkMode ? '#121212' : '#ffffff', 
      color: data.theme.darkMode ? '#ffffff' : '#121212',
      minHeight: '100vh',
      padding: '40px',
      fontFamily: 'sans-serif'
    }}>
      <h1 style={{ color: data.theme.primaryColor }}>
        {data.sections[0].data.title}
      </h1>
      <p style={{ fontSize: '1.2rem' }}>
        {data.sections[0].data.subtitle}
      </p>
      
      <div style={{ marginTop: '40px', borderTop: '1px solid #333', paddingTop: '20px' }}>
        <h2>About Me</h2>
        <p>{data.sections[1].data.text}</p>
      </div>
    </div>
  );
}